<style lang="less">
</style>
<template>
  <div class="row">
    <div class="col-3 list-group-left">
      <h3>展示列表</h3>
      <draggable class="list-group " :list="checkColumns" group="people" @change="columnChange">
        <div
            class="list-group-item"
            v-for="(element, index) in checkColumns"
            :key="index">
          {{ element.pname }}
        </div>
      </draggable>
    </div>

    <div class="col-3 list-group-right">
      <h3>隐藏列表</h3>
      <draggable class="list-group " :list="unCheckColumns" group="people" @change="columnChange">
        <div
            class="list-group-item"
            v-for="(element, index) in unCheckColumns"
            :key="index">
          {{ element.pname }}
        </div>
      </draggable>
    </div>
  </div>
</template>

<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"
import {getPColumns} from "@/api/laisonorder";
import draggable from "vuedraggable"

export default {
  name: "setting-form",
  mixins: [JeecgListMixin],
  components: {
    draggable
  },
  props: {
    processCode: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      idGlobal: 0,
      processColumns: [],
      checkColumns: [],
      unCheckColumns: []
    }
  },
  created() {
    this.getProcessColumns()
  },
  watch: {
    processCode: function (newval, oldval) {
      console.log("value", newval, oldval)
    }
  },
  methods: {
    showMessage(msg) {
      this.$message.loading(msg)
    },
    hideMessage() {
      this.$message.destroy()
    },
    getProcessColumns() {
      this.showMessage("加载中...")
      getPColumns(this.processCode).then((res) => {
        this.hideMessage()
        if (res) {
          this.processColumns = res.result
          this.checkColumns = this.selectColumns(0)
          this.unCheckColumns = this.selectColumns(1)
        }
      }).catch((err) => {
        this.hideMessage()
      })
    },
    selectColumns(type) {
      let result = []
      if (this.processColumns != null && this.processColumns.length > 0) {
        for (let i = 0; i < this.processColumns.length; i++) {
          let processColumn = this.processColumns[i]
          if (processColumn.type === type) {
            result.push(processColumn)
          }
        }
      }
      return result
    },
    columnChange() {
      this.$emit("checkColumns", this.checkColumns)
      this.$emit("unCheckColumns", this.unCheckColumns)
    },
  }
}
</script>

<style lang='less' scoped>
.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: 10px;
  margin-left: 10px;
}

.col-3 {
  -ms-flex: 0 0 25%;
  flex: 0 0 25%;
  max-width: 25%;
}

h3 {
  font-size: 18px;
  font-weight: bold;
}

.list-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
  border-radius: 0.25rem;
}

.list-group-left {
  margin-right: 30px;
}

.list-group-right {
  margin-left: 30px;
}

.list-group-item {
  position: relative;
  display: block;
  padding: 0.15rem 0.5rem;
  background-color: #fff;
  text-align: center;
  margin-top: 3px;
  border: 1px solid rgba(0, 0, 0, 0.125);
}
</style>

